<template>
  <div class="wrapper" ref="wrapper" @click="clickGround">
    <div
      class="cross-shadow"
      v-if="moving"
      :style="`transform: translate(${tempChess.x}px,${tempChess.y}px)`"
    >
      <!-- ÂçÅÂ≠óÊòü -->
    </div>
    <div v-for="item in chessmans" style="position:relative" :key="item.id">
      <div
        class="piece"
        :class="
          `${chessTypes[item.type]} ${
            item.active ? 'active' : ''
          } ${item.status||''}`
        "
        :style="`transform: translate(${item.x}px,${item.y}px)`"
      ></div>
      <div v-if="item.active" class="piece scaleAni"
        :style="`top:${item.y}px;left:${item.x}px`"
      ></div>
    </div>
    <div
      class="piece moving"
      v-if="tempChess"
      :class="chessTypes[tempChess.type]"
      :style="`transform: translate(${tempChess.x}px,${tempChess.y}px)`"
    >
      <!-- ‚ÄúËêΩÂ≠ê‚ÄùÊåâÈíÆ -->
      <div
        v-if="tempChess && !moving"
        class="confirmBtn1"
        @click.stop="clickConfirmBtn"
      ></div>
    </div>
  </div>
</template>

<script>
const screenWidth =
  document.documentElement.scrollWidth || document.body.scrollWidth
const COLUMNS = 14 // Ê£ãÁõòÂàóÊï∞
const MARGIN = (COLUMNS * screenWidth) / 750 // Ê£ãÁõòÂ§ñËæπÁïå
const PADDING = (28 * screenWidth) / 750 // Ê£ãÁõòÂÜÖËæπÁïå
const OUTWIDTH = MARGIN + PADDING // Ê£ãÁõòÈùûÊ¥ªÂä®Âå∫ÂüüÁöÑÊÄªÂÆΩÂ∫¶
const RECTWIDTH = (50 * screenWidth) / 750 // Ê£ãÊ†ºÁöÑÂÆΩÂ∫¶

const chessBoard = [] // Ê®°ÊãüÊ£ãÁõòËêΩÂ≠êÁöÑ‰∫åÁª¥Êï∞ÁªÑ
const wins = [] // Ëµ¢Ê≥ïÁªüËÆ°Êï∞ÁªÑ
let count = 0 // Ëµ¢Ê≥ïÁöÑÊÄªÊï∞ÁõÆ
// ‰∏§ÊñπËßíËâ≤Ëµ¢Ê≥ïÁªüËÆ°Êï∞ÁªÑ
const myWin = []
const AIWin = []
// ËßíËâ≤Â∏∏Èáè
const Me = 'Me'
const Competitor = 'Competitor'
const chessTypes = { Competitor: 'white', Me: 'black' } // Ê£ãÂ≠êÈ¢úËâ≤
// AIÊÄùËÄÉÊó∂Èó¥ÔºàÊ®°ÊãüÔºâ
// const THINKTIME = 2000
export default {
  name: 'gobang',
  data () {
    return {
      chessmans: [], // Â≠òÊîæÊ£ãÁõò‰∏äÂ∑≤ÊúâÁöÑÊ£ãÂ≠ê ÁöÑ‰ø°ÊÅØ
      tempChess: null, // Áé©ÂÆ∂ÁßªÂä®ÈÄâÁÇπÊó∂ÁöÑÊ£ãÂ≠ê
      moving: false,
      chessTypes, // Ê£ãÂ≠êÈ¢úËâ≤
      Me, // ÊàëÁöÑËßíËâ≤
      Competitor, // ÂØπÊâãÁöÑËßíËâ≤
      players: { Me, Competitor },
      competitors: { Me: Competitor, Competitor: Me },
      playing: Me, // ÂΩìÂâçÊâßÊ£ã‰∫∫ÔºåÁªìÊùüÊó∂‰∏∫null
      winner: null
    }
  },
  watch: {
    tempChess (newval) {
      this.$emit('onAction', newval !== null)
    }
  },
  created () {
    this.init()
  },
  mounted () {
    this.getWrapperInfo()
    this.initTouchEvent()
  },
  methods: {
    // ÂàùÂßãÂåñÊ£ãÁõòÂíåÊï∞ÁªÑ
    init () {
      this.initWinType()
      this.cleanChessboard()
    },
    // ÂàùÂßãÂåñËµ¢Ê≥ïÁªüËÆ°Êï∞ÁªÑÔºåÂè™ÈúÄË¶ÅÊâßË°å‰∏ÄÊ¨°ÔºåÊâÄ‰ª•initÊñπÊ≥ï‰∏çÈúÄË¶ÅÂÜçË∞É
    initWinType () {
      for (let i = 0; i < COLUMNS; i++) {
        wins[i] = []
        for (let j = 0; j < COLUMNS; j++) {
          wins[i][j] = []
        }
      }
      // Èò≥Á∫øÁ∫µÂêë90¬∞ÁöÑËµ¢Ê≥ï
      for (let i = 0; i < COLUMNS; i++) {
        for (let j = 0; j < COLUMNS - 4; j++) {
          for (let k = 0; k < 5; k++) {
            wins[i][j + k][count] = true
          }
          count++
        }
      }

      // Èò≥Á∫øÊ®™Âêë0¬∞ÁöÑËµ¢Ê≥ï
      for (let i = 0; i < COLUMNS; i++) {
        for (let j = 0; j < COLUMNS - 4; j++) {
          for (let k = 0; k < 5; k++) {
            wins[j + k][i][count] = true
          }
          count++
        }
      }

      // Èò¥Á∫øÊñúÂêë135¬∞ÁöÑËµ¢Ê≥ï
      for (let i = 0; i < COLUMNS - 4; i++) {
        for (let j = 0; j < COLUMNS - 4; j++) {
          for (let k = 0; k < 5; k++) {
            wins[i + k][j + k][count] = true
          }
          count++
        }
      }

      // Èò¥Á∫øÊñúÂêë45¬∞ÁöÑËµ¢Ê≥ï
      for (let i = 0; i < COLUMNS - 4; i++) {
        for (let j = COLUMNS - 1; j > 3; j--) {
          for (let k = 0; k < 5; k++) {
            wins[i + k][j - k][count] = true
          }
          count++
        }
      }
    },
    // ÂõûÂΩíÂàùÂßãÁä∂ÊÄÅ,Âπ∂Êèê‰æõ‰∏Ä‰∏™ÂàùÂßãÁé©ÂÆ∂
    cleanChessboard (player = Me) {
      this.chessmans = []
      this.playing = player
      this.tempChess = null
      this.winner = null
      // ÂàùÂßãÂåñÊ£ãÁõò
      for (let i = 0; i < COLUMNS; i++) {
        chessBoard[i] = []
        for (let j = 0; j < COLUMNS; j++) {
          chessBoard[i][j] = 0
        }
      }
      // ÂàùÂßãÂåñÂèåÊñπËµ¢Ê≥ïÁöÑÁªüËÆ°Êï∞ÁªÑ
      for (let i = 0; i < count; i++) {
        myWin[i] = 0
        AIWin[i] = 0
      }
    },
    getWrapperInfo () {
      const dom = this.$refs.wrapper
      const { left, top } = dom.getBoundingClientRect()
      dom.wrapperX = left
      dom.wrapperY = top
      return dom
    },
    initTouchEvent () {
      let startX = 0
      let startY = 0
      let endX = 0
      let endY = 0
      let tmp = {}
      const dom = this.$refs.wrapper
      const { wrapperX, wrapperY } = dom
      // touchStart‰∫ã‰ª∂
      dom.addEventListener(
        'touchstart',
        event => {
          if (this.playing !== 'Me') {
            return
          }
          const touch = event.touches[0] || event
          startX = touch.pageX
          startY = touch.pageY
          if (!this.tempChess) {
            this.clickGround({ // Ëß¶Êë∏ÁöÑÊó∂ÂÄôÁîüÊàê‰∏¥Êó∂Ê£ãÂ≠êÔºà‰πãÂâçÊòØÂçïÁã¨ÁöÑÁÇπÂáª‰∫ã‰ª∂Ôºâ
              x: startX,
              y: startY
            })
          }
          tmp = this.tempChess
        },
        false
      )
      // touchmove‰∫ã‰ª∂
      dom.addEventListener(
        'touchmove',
        event => {
          if (this.playing !== 'Me') {
            return
          }
          if (!this.tempChess) {
            return
          }
          const touch = event.touches[0] || event
          endX = touch.pageX
          endY = touch.pageY
          const offsetX = endX - startX // Âü∫‰∫étouch‰∫ã‰ª∂ÂàùÂßã‰ΩçÁΩÆÁöÑÂÅèÁßªÈáè
          const offsetY = endY - startY
          const x = wrapperX + tmp.x + offsetX // Ê£ãÂ≠êÂàùÂßã‰ΩçÁΩÆÂä†‰∏äÂÅèÁßªÈáèËé∑ÂæóÁöÑÂÖ®Â±ÄxÔºåyÂùêÊ†áÔºàÊ£ãÂ≠êÂØπË±°ÁöÑxÔºåyÊòØÂü∫‰∫éÁà∂ÂÆπÂô®ÁöÑÔºåÊâÄ‰ª•Ë¶ÅÂä†wrapperXYÔºâ
          const y = wrapperY + tmp.y + offsetY
          const { indexX, indexY } = this.transformXY(x, y) // ËΩ¨Êç¢ÂùêÊ†áËé∑ÂæóÊ£ãÂ≠ê‰ΩçÁΩÆÁöÑÂ∫èÂè∑
          // console.log({ indexX, indexY })
          this.tempChess = this.getChess(indexX, indexY, 'Me') // Êõ¥Êñ∞‰∏¥Êó∂Ê£ãÂ≠ê
          this.moving = true
        },
        false
      )
      // touchend‰∫ã‰ª∂
      dom.addEventListener(
        'touchend',
        () => {
          if (this.playing !== 'Me') {
            return
          }
          if (!this.tempChess) {
            return
          }
          this.moving = false
          const { indexX, indexY } = this.tempChess
          // ÁõÆÁöÑÂú∞ÊúâÂÖ∂‰ªñÊ£ãÂ≠êÔºåÂàôÂõûÂà∞touchÊúÄÂàùÁä∂ÊÄÅ
          if (chessBoard[indexX][indexY]) {
            this.tempChess = tmp
          }
        },
        false
      )
    },
    // Á°ÆËÆ§ËêΩÂ≠ê
    clickConfirmBtn () {
      if (!this.tempChess) {
        return
      }
      const { indexX, indexY } = this.tempChess
      this.drop(indexX, indexY, 'Me')
      // Ê£ÄÊµãÊòØÂê¶Ëµ¢ÔºåÊ≤°Ëµ¢Êç¢Êâã
      this.handleDrop(indexX, indexY, 'Me')
    },
    // ÁÇπÂáªÊ£ãÊùøÁîüÊàê‰∏¥Êó∂Ê£ãÂ≠ê
    clickGround (e) {
      if (this.playing !== 'Me') {
        return
      }
      // ÁÇπÂáªÁöÑÂùêÊ†á
      const { x, y } = e
      const { indexX, indexY } = this.transformXY(x, y)
      // console.log(indexX)
      if (!chessBoard[indexX][indexY]) {
        this.tempChess = this.getChess(indexX, indexY, 'Me')
      }
    },
    /**
     * Â∞ÜÂü∫‰∫éÈ°µÈù¢Ôºà‰∏çÊòØÂÆπÂô®ÔºâxyÂùêÊ†áÁõ¥Êé•ËΩ¨Êç¢‰∏∫ÊúÄËøë‰∏Ä‰∏™Ê£ãÂ≠êÁöÑindexX,indexYÁºñÂè∑
     * @param eventX
     * @param eventY
     * */
    transformXY (eventX, eventY) {
      // Ê£ãÁõòÂõæÁâáÂ∑¶‰∏äËßíÂú®È°µÈù¢‰∏äÁöÑx„ÄÅyÂÄº
      const {
        left: wrapperX,
        top: wrapperY
      } = this.$refs.wrapper.getBoundingClientRect()
      const minXY = 0 // „ÄêÈôêÂÆö„ÄëÂú®Ê£ãÁõò‰∏äÁöÑÊúÄÂ∞èx„ÄÅyÂÄº
      const maxXY = RECTWIDTH * (COLUMNS - 1) // „ÄêÈôêÂÆö„ÄëÂú®Ê£ãÁõò‰∏äÁöÑÊúÄÂ§ßx„ÄÅyÂÄº
      const originX = wrapperX + OUTWIDTH // Ê£ãÁõòÁöÑÂéüÁÇπÂùêÊ†áx
      const originY = wrapperY + OUTWIDTH // Ê£ãÁõòÁöÑÂéüÁÇπÂùêÊ†áy
      let realX = eventX - originX // Ê£ãÂ≠êÂü∫‰∫éÊ£ãÁõòÂéüÁÇπÁöÑÂùêÊ†áx
      let realY = eventY - originY // Ê£ãÂ≠êÂü∫‰∫éÊ£ãÁõòÂéüÁÇπÁöÑÂùêÊ†áy
      realX = realX < minXY ? minXY : realX > maxXY ? maxXY : realX // ÈôêÂà∂ÊúÄÂ§ßÂÄºÊúÄÂ∞èÂÄºÈò≤Ê≠¢Ë∂ÖÂá∫Ê£ãÁõò
      realY = realY < minXY ? minXY : realY > maxXY ? maxXY : realY // Âêå‰∏ä
      const indexX = Math.round(realX / RECTWIDTH)
      const indexY = Math.round(realY / RECTWIDTH)
      return { indexX, indexY }
    },
    /**
     * Ê†πÊçÆÊ£ãÂ≠êÁºñÂè∑ËΩ¨Êç¢Ê£ãÂ≠êÁöÑÂÆûÈôÖÂùêÊ†áÁÇπxy, ËøîÂõû‰∏Ä‰∏™Ê£ãÂ≠êÂØπË±°
     * @param eventX
     * @param eventY
     * @param type = ['Me', 'Competitor']
     * */
    getChess (indexX, indexY, type = 0) {
      const x = OUTWIDTH + indexX * (RECTWIDTH + 0.5)
      const y = OUTWIDTH + indexY * (RECTWIDTH + 0.5)
      return {
        indexX,
        indexY,
        x,
        y,
        status: '',
        tmpx: x,
        tmpy: y,
        type,
        index: this.chessmans.length
      }
    },
    /**
     * Ê∏≤ÊüìÊ£ãÂ≠êÂØπË±°
     * @param chess Ê£ãÂ≠êÂØπË±°
     * */
    renderChess (chess) {
      const { indexX, indexY } = chess
      this.chessmans.push(chess)
      chessBoard[indexX][indexY] = chess
      this.refreshActive()
      return [indexX, indexY]
    },
    /**
     * Ê∏≤ÊüìÊ£ãÂ≠êÔºàÂØπÂ§ñÊö¥Èú≤Ôºâ
     * @param indexX Ê£ãÂ≠êÂü∫Êú¨ÂèÇÊï∞
     * @param indexY Ê£ãÂ≠êÂü∫Êú¨ÂèÇÊï∞
     * @param type Ê£ãÂ≠êÂü∫Êú¨ÂèÇÊï∞
     * */
    drawChess (indexX, indexY, type) {
      this.renderChess(this.getChess(indexX, indexY, type))
    },
    // ‰∏ãÊ£ãËêΩÂ≠êÔºåÂêåÊó∂Âπ∂‰∏äÊä•ÁªôÁà∂ÁªÑ‰ª∂
    drop (indexX, indexY, type = Competitor) {
      this.drawChess(indexX, indexY, type)
      // ÂâîÈô§‰∏¥Êó∂Ê£ãÂ≠ê
      this.tempChess = null
      this.$emit('onDrop', this.chessmans.slice(-1)[0], this.chessmans)
      console.log('gobang playing', this.playing)
    },
    // Â§ÑÁêÜ‰∏ä‰∏ÄÊ≠•ËêΩÂ≠êÂêéÁöÑ‰∫ãÂä°
    handleDrop (indexX, indexY, type = Competitor) {
      // ËÆ°ÁÆóÂΩìÂâçÁî®Êà∑Êìç‰ΩúÁöÑÁªìÊûú
      const winner = this.checkWin(indexX, indexY, type)
      // Ëµ¢
      if (winner) {
        this.endGame(type)
      } else {
        // Ê≤°Ëµ¢ÔºåÊç¢Êâã
        this.togglePlayer()
      }
    },
    // ËÆæÁΩÆÊúÄÂêé‰∏Ä‰∏™Ê£ãÂ≠êÁöÑÊøÄÊ¥ªÁä∂ÊÄÅ
    refreshActive () {
      const lastIndex = this.chessmans.length - 1
      const prev = this.chessmans[lastIndex - 1]
      const last = this.chessmans[lastIndex]
      if (prev) {
        prev.active = false
      }
      if (last) {
        last.active = true
      }
    },
    /**
     * Ê£ÄÊü•ÊòØÂê¶ËÉúÂà©
     * @param indexX Â∞ÜË¶ÅÊ£ÄÊü•ÁöÑÊ£ãÂ≠êÁöÑXÂ∫èÂè∑
     * @param indexX Â∞ÜË¶ÅÊ£ÄÊü•ÁöÑÊ£ãÂ≠êÁöÑYÂ∫èÂè∑
     * @param who ËøôÊòØË∞Å‰∏ãÁöÑ
     */
    checkWin (indexX, indexY, who) {
      const winTypeNow = wins[indexX][indexY]
      const target = who === 'Me' ? myWin : AIWin
      const otherOne = who === 'Me' ? AIWin : myWin
      let winner = false
      // ÈÅçÂéÜËµ¢Ê≥ïÁªüËÆ°Êï∞ÁªÑ
      for (let k = 0; k < count; k++) {
        if (winTypeNow[k]) {
          // Â¶ÇÊûúÂ≠òÂú®Ëµ¢Ê≥ï,ÂàôÂΩìÂâçÁé©ÂÆ∂Ê≠§Ëµ¢Ê≥ïËÉúÁÆó+1(Ëµ¢Ê≥ï‰∏∫5ËÉúÂèñËÉú)
          target[k]++
          // Â¶ÇÊûúÂ≠òÂú®Ëµ¢Ê≥ï,ÂàôÂØπÊâãÊ≠§Ëµ¢Ê≥ïËÉúÁÆóËµãÂÄº‰∏∫6(Ê∞∏Ëøú‰∏çÁ≠â‰∫é5,Ê∞∏ËøúÊó†Ê≥ïÂú®Ê≠§Â§ÑÂèñËÉú)
          otherOne[k] = 6
          // ÁõÆÊ†áËêΩÂ≠êÂêé,Ê≠§Â§ÑËµ¢Ê≥ïÊï∞ÁªÑÂáëÂ§ü5,Áé©ÂÆ∂ÂèñËÉú
          if (target[k] === 5) {
            console.log(who + ' Win')
            winner = who
            this.getWinList(indexX, indexY)
            return winner
          }
        }
      }
      return winner
    },
    // È´ò‰∫ÆËµ¢ÁöÑÊ£ãÂ≠ê
    getWinList (x, y) {
      if (x === undefined || y === undefined) {
        return
      }
      const current = chessBoard[x][y]
      // Ê®™Âêë
      const st1 = []
      for (let i = -1; i > -5; i--) {
        if (
          chessBoard[x + i] &&
          chessBoard[x + i][y] &&
          chessBoard[x + i][y].type === current.type
        ) {
          st1.push(chessBoard[x + i][y])
        } else {
          break
        }
      }
      for (let i = 0; i < 5; i++) {
        if (
          chessBoard[x + i] &&
          chessBoard[x + i][y] &&
          chessBoard[x + i][y].type === current.type
        ) {
          st1.push(chessBoard[x + i][y])
        } else {
          break
        }
      }
      // Á´ñÂêë
      const st2 = []
      for (let i = -1; i > -5; i--) {
        if (
          chessBoard[x] &&
          chessBoard[x][y + i] &&
          chessBoard[x][y + i].type === current.type
        ) {
          st2.push(chessBoard[x][y + i])
        } else {
          break
        }
      }
      for (let i = 0; i < 5; i++) {
        if (
          chessBoard[x] &&
          chessBoard[x][y + i] &&
          chessBoard[x][y + i].type === current.type
        ) {
          st2.push(chessBoard[x][y + i])
        } else {
          break
        }
      }
      // Êñú1 ‚ÜòÔ∏è
      const st3 = []
      for (let i = -1; i > -5; i--) {
        if (
          chessBoard[x + i] &&
          chessBoard[x + i][y + i] &&
          chessBoard[x + i][y + i].type === current.type
        ) {
          st3.push(chessBoard[x + i][y + i])
        } else {
          break
        }
      }
      for (let i = 0; i < 5; i++) {
        if (
          chessBoard[x + i] &&
          chessBoard[x + i][y + i] &&
          chessBoard[x + i][y + i].type === current.type
        ) {
          st3.push(chessBoard[x + i][y + i])
        } else {
          break
        }
      }
      // Êñú2 ‚ÜôÔ∏è
      const st4 = []
      for (let i = -1; i > -5; i--) {
        if (
          chessBoard[x - i] &&
          chessBoard[x - i][y + i] &&
          chessBoard[x - i][y + i].type === current.type
        ) {
          st4.push(chessBoard[x - i][y + i])
        } else {
          break
        }
      }
      for (let i = 0; i < 5; i++) {
        if (
          chessBoard[x - i] &&
          chessBoard[x - i][y + i] &&
          chessBoard[x - i][y + i].type === current.type
        ) {
          st4.push(chessBoard[x - i][y + i])
        } else {
          break
        }
      }
      const temp = [st1, st2, st3, st4]
      // console.log(temp)
      temp.forEach(v => {
        if (v.length === 5) {
          v.forEach((chess) => {
            chess.status = 'win'
          })
        }
      })
    },
    // ÁªìÊùüÊ∏∏ÊàèÔºåËæìÂá∫Ëµ¢ËÄÖ
    endGame (who) {
      this.playing = null
      this.tempChess = null
      this.winner = who
      setTimeout(() => {
        if (!who || who === 'Competitor') {
          alert('‰Ω†Ëæì‰∫ÜÔΩû„ÄÇ0')
        } else {
          alert('‰Ω†Ëµ¢‰∫ÜÔºåÂéâÂÆ≥‰∫ÜÂéâÂÆ≥‰∫ÜÔºÅ')
        }
        this.$emit('onEnd', who, this.chessmans)
      }, 3600)
    },
    // ÂàáÊç¢ÂΩìÂâçÁé©ÂÆ∂ÔºåÊîπÂèòthis.playing
    togglePlayer (setPlayer) {
      const playing = this.playing
      if (setPlayer !== undefined) {
        this.playing = setPlayer
      } else if (playing) {
        this.playing = this.competitors[playing]
      }
    },
    // AI‰∏ãÊ£ã
    AIDO () {
      let u = 0 // ÁîµËÑëÈ¢ÑËêΩÂ≠êÁöÑx‰ΩçÁΩÆ
      let v = 0 // ÁîµËÑëÈ¢ÑËêΩÂ≠êÁöÑy‰ΩçÁΩÆ
      let myScore = [] // Áé©ÂÆ∂ÁöÑÂàÜÊï∞
      let AIScore = [] // ÁîµËÑëÁöÑÂàÜÊï∞
      let max = 0 // ÊúÄ‰ºò‰ΩçÁΩÆÁöÑÂàÜÊï∞
      // ÂàùÂßãÂåñÂàÜÊï∞ÁöÑ‰∫åÁª¥Êï∞ÁªÑ
      for (let i = 0; i < COLUMNS; i++) {
        myScore[i] = []
        AIScore[i] = []
        for (let j = 0; j < COLUMNS; j++) {
          myScore[i][j] = 0
          AIScore[i][j] = 0
        }
      }
      // ÈÄöËøáËµ¢Ê≥ïÁªüËÆ°Êï∞ÁªÑ‰∏∫‰∏§‰∏™‰∫åÁª¥Êï∞ÁªÑÂàÜÂà´ËÆ°ÂàÜ
      for (let i = 0; i < COLUMNS; i++) {
        for (let j = 0; j < COLUMNS; j++) {
          if (chessBoard[i][j] === 0) {
            for (let k = 0; k < count; k++) {
              if (wins[i][j][k]) {
                if (myWin[k] === 1) {
                  myScore[i][j] += 200
                } else if (myWin[k] === 2) {
                  myScore[i][j] += 400
                } else if (myWin[k] === 3) {
                  myScore[i][j] += 2000
                } else if (myWin[k] === 4) {
                  myScore[i][j] += 10000
                }
                if (AIWin[k] === 1) {
                  AIScore[i][j] += 220
                } else if (AIWin[k] === 2) {
                  AIScore[i][j] += 420
                } else if (AIWin[k] === 3) {
                  AIScore[i][j] += 2100
                } else if (AIWin[k] === 4) {
                  AIScore[i][j] += 20000
                }
              }
            }

            // Â¶ÇÊûúÁé©ÂÆ∂(i,j)Â§ÑÊØîÁõÆÂâçÊúÄ‰ºòÁöÑÂàÜÊï∞Â§ßÔºåÂàôËêΩÂ≠êÂú®(i,j)Â§Ñ
            if (myScore[i][j] > max) {
              max = myScore[i][j]
              u = i
              v = j
            } else if (myScore[i][j] === max) {
              // Â¶ÇÊûúÁé©ÂÆ∂(i,j)Â§ÑÂíåÁõÆÂâçÊúÄ‰ºòÂàÜÊï∞‰∏ÄÊ†∑Â§ßÔºåÂàôÊØîËæÉÁîµËÑëÂú®ËØ•‰ΩçÁΩÆÂíåÈ¢ÑËêΩÂ≠êÁöÑ‰ΩçÁΩÆÁöÑÂàÜÊï∞
              if (AIScore[i][j] > AIScore[u][v]) {
                u = i
                v = j
              }
            }

            // Â¶ÇÊûúÁîµËÑë(i,j)Â§ÑÊØîÁõÆÂâçÊúÄ‰ºòÁöÑÂàÜÊï∞Â§ßÔºåÂàôËêΩÂ≠êÂú®(i,j)Â§Ñ
            if (AIScore[i][j] > max) {
              max = AIScore[i][j]
              u = i
              v = j
            } else if (AIScore[i][j] === max) {
              // Â¶ÇÊûúÁîµËÑë(i,j)Â§ÑÂíåÁõÆÂâçÊúÄ‰ºòÂàÜÊï∞‰∏ÄÊ†∑Â§ßÔºåÂàôÊØîËæÉÁé©ÂÆ∂Âú®ËØ•‰ΩçÁΩÆÂíåÈ¢ÑËêΩÂ≠êÁöÑ‰ΩçÁΩÆÁöÑÂàÜÊï∞
              if (myScore[i][j] > myScore[u][v]) {
                u = i
                v = j
              }
            }
          }
        }
      }
      // ‰∏ãÊ£ã
      this.drop(u, v, 'Competitor')
      // Ê£ÄÊµãÊòØÂê¶Ëµ¢ÔºåÊ≤°Ëµ¢Êç¢Êâã
      this.handleDrop(u, v, 'Competitor')
    }
  }
}
</script>

